.App {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;

}
.SuperBasic {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

.container_ProfilePhoto, .container_InfoAndContact {
    margin: 10px 25px;
    z-index: 100;
}

.container_ProfilePhoto > div {
    background-size: cover;
    -webkit-box-shadow: 10px 5px 5px gray;
            box-shadow: 10px 5px 5px gray;
    -webkit-filter: gray;
            filter: gray;
    height: 150px;
    -webkit-filter: grayscale(100%);
    width: 150px;
}

.container_Info h2 {
  margin: 0;
  padding: 0;
}
.content_info{
  white-space: nowrap;
}

.container_Icons a{
  margin-right: 10px;
}

.container_Projects {
   position: fixed;
   bottom: 5vh;
}

.container_Projects a {
  color: black;
  text-decoration: none;
  margin: 0 10px;
}

.titles {
  white-space: nowrap;
}

.container_Modal {
    text-align: center;
}

.container_Modal span {
    font-weight: 700;
    text-decoration: underline;
}

.container_Projects div div {
  display: none;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@media only screen and (max-width: 630px) {
  /*for more narrow screens, go for column, not row*/
  .SuperBasic {
    -ms-flex-direction: column;
        flex-direction: column;
  }
}



